import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NotFoundComponent } from './main/not-found/not-found.component';

// angular routing for multiple views
const routes: Routes = [
  { path: '', loadChildren: () => import('./main/dashboard/dashboard.module').then(m => m.DashboardModule), },
  { path: 'clients', loadChildren: () => import('./main/clients/clients.module').then(m => m.ClientsModule), },
  { path: 'projects', loadChildren: () => import('./main/projects/projects.module').then(m => m.ProjectsModule), },
  { path: 'resources', loadChildren: () => import('./main/resources/resources.module').then(m => m.ResourcesModule), },
  { path: 'skills', loadChildren: () => import('./main/skills/skills.module').then(m => m.SkillsModule), },
  { path: '404', component: NotFoundComponent },
  { path: '**', redirectTo: '/404' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
