import { Component, OnInit } from '@angular/core';
import { faTh, faThList, faUserFriends, faUsersCog, faLaptopCode } from '@fortawesome/free-solid-svg-icons';
import { SidebarService } from 'src/app/core/sidebar/sidebar.service';

@Component({
  selector: 'app-leftbar',
  templateUrl: './leftbar.component.html',
  styleUrls: ['./leftbar.component.scss']
})
export class LeftbarComponent implements OnInit {
  icons = [faTh, faThList, faUsersCog, faUserFriends, faLaptopCode];

  constructor(private sidebarService: SidebarService) { }
  ngOnInit(): void {
  }
  get isSidebarVisible(): boolean {
    return this.sidebarService.isSidebarVisible;
  }
  scrolltop = () => {
    window.scrollTo(0, 0);
  }
}
